(function() {
  'use strict';
  /**
   * @ngdoc overview
   * @name tamedia
   * @description
   * Main module of the tamedia application.
   */
  angular
    .module('tamedia', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'toastr']);
})();

(function() {
    'use strict';
    /**
     * @ngdoc service
     * @name tamedia.factory:apiFactory
     *
     * @description
     * POST method of the Account API
     *
     * @requires $http
     * @requires apiEndpoint
     *
     */
    angular.module('tamedia')
        .factory('apiFactory', apiFactory);

      function apiFactory(
        $http, apiEndpoint
      ){
        var apiFactoryFactory = {
            // GET Methods
            getProductList: getProductList
        };

        return apiFactoryFactory;

        /**
         * @ngdoc
         * @name tamedia.factory:apiFactory#getProductList
         * @methodOf tamedia.factory:apiFactory
         *
         * @description
         * GET method of the Account API
         *
         * @param {object} paramsObject Accepts params object in this format:
         * <pre>
         * params:{
         *  nbArticles: 20
         * }
         * </pre>
        */
        function getProductList(params) {
          return $http.get(apiEndpoint.proxy + '/articles', params);
        }

    }
}());

(function() {
  'use strict';

  angular
    .module('tamedia')
    .directive('singleProduct', SingleProduct);

  /** @ngInject */
  function SingleProduct() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/single_product/single.product.html',
      scope: {
          product: '=product'
      },
      controller: SingleProductController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function SingleProductController() {

    }
  }

})();

(function() {
  'use strict';

  angular
    .module('tamedia')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope:{
        title: '=',
        backButton: '=',
        backCallback: '='
      }
    };

    return directive;
  }

})();

(function() {
  'use strict';

  /**
   * @ngdoc service
   * @name tamedia.controller:MainController
   *
   * @description
   * Home view controller
   *
   * @requires productList
   */

  angular
    .module('tamedia')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(productList) {
    var vm = this;
    vm.pageTitle = "Latest products";
    vm.productList = productList.data.articles;
    
    vm.showSingleProd = function(item){
      vm.singleProductOpen = true;
      vm.pageTitle = "Product overview"
    }

    vm.backCallback = function(){
      vm.singleProductOpen = false;
      vm.pageTitle = "Latest products";
    }
  }
})();

(function() {
  'use strict';

  /**
  * @ngdoc overview
  * @name tamedia.run
  *
  * @description
  * Front-end application run block.
  * Runs only once, right after application bootstrap!
  */

  angular
    .module('tamedia')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope) {
    var scope = $rootScope;
    scope.$on('$stateChangeStart',function(){
      scope.stateIsLoading = true;
    });

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';
  /**
   * @ngdoc overview
   * @name router
   *
   * @description
   * Application routes
   *
   */

  angular
    .module('tamedia')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      /**
       * @ngdoc overview
       * @name tamedia.route:home
       *
       * @description
       * Home screen route, showing product list
       *
       * @requires tamedia.factory:apiFactory
       * @requires tamedia.controller:MainController
       */
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main',
        resolve: {
          productList: /** @ngInject */
            function (apiFactory) {
              var params = {
                nbArticles: 20
              };

              return apiFactory.getProductList(params);
            }
        }
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global moment:false */
(function() {
  'use strict';

  /**
   * @ngdoc configuration
   * @name tamedia.config
   * @methodOf tamedia.config
   *
   * @description
   * tamedia application constans
   *
   */

  angular
    .module('tamedia')
      .constant('moment', moment)
      .constant('apiEndpoint', {
          host: 'https://www.ricardo.ch',
          proxy: 'http://192.168.0.141:8090'
      })
})();

(function() {
  'use strict';

  /**
   * @ngdoc configuration
   * @name tamedia.config
   * @methodOf tamedia.config
   *
   * @description
   * tamedia application configuration
   *
   */
  angular
    .module('tamedia')
    .config(config);

  /** @ngInject */
  function config($logProvider) {
    // Enable log
    $logProvider.debugEnabled(true);
  }

})();

angular.module("tamedia").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"product-list\" ng-class=\"{\'open\': main.singleProductOpen}\"><acme-navbar title=\"main.pageTitle\" back-button=\"main.singleProductOpen\" back-callback=\"main.backCallback\"></acme-navbar><div class=\"container main-view-holder\"><single-product ng-class=\"{opened: main.singleProductOpen}\"></single-product><div class=\"row\"><ul class=\"container-list\"><li ng-repeat=\"item in main.productList\" ng-click=\"main.showSingleProd(item)\" class=\"clearfix\"><div class=\"col-sm-6\"><img ng-src=\"{{item.PictureUrl}}\" src=\"./assets/images/loading_spinner.gif\" alt=\"\" class=\"img-center-xs\"></div><div class=\"col-sm-6 text-left text-center-xs\">{{item.Title}}<br><b>Price:&nbsp;</b>{{item.Price}}<br><b>Buy Now Price:&nbsp;</b>{{item.BuyNowPrice}}</div><div class=\"col-lg-2 col-sm-2\"></div><div class=\"col-lg-10 col-sm-10\"></div></li></ul></div></div></div>");
$templateCache.put("app/components/single_product/single.product.html","<div class=\"single-product\"></div>");
$templateCache.put("app/components/navbar/navbar.html","<div class=\"head-stripe\"><div class=\"container\"><h2 ng-class=\"{backShow: backButton}\" ng-click=\"backCallback()\"><span ng-if=\"backButton\"><span class=\"glyphicon glyphicon-arrow-left\" aria-hidden=\"true\"></span></span> {{title}}</h2></div></div>");}]);